import React from 'react'
import styled from 'styled-components'
import {LanguageContext} from "../Context/LanguageProvider";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import getText from "../i18n";
import CustomSlide from "./CustomSilde";

const squarePoints = process.env.PUBLIC_URL + '/assets/img/squarePointStorage.png';

class LocationSectionHome extends React.Component {
  static contextType = LanguageContext;
  state = {
    boxesArray: []
  }

  componentDidMount() {
    fetch('https://my-self-storage.herokuapp.com/API/BoxPricings/?format=json')
      .then((response) => response.json())
      .then(boxesArray => this.setState({boxesArray}))
      .catch(function (error) {
        console.log('Il y a eu un problème avec l\'opération fetch: ' + error.message);
      });
  }

  render() {
    return (
      <Container className="locationSection">
        <div className="container">
          <div className="wrapper">
            <div className="row">
              <div className={"col-12 col-md-4"}>
                <h2>Location de box</h2>
                <h5>Consultez nos prix en fonction
                  de tailles de box </h5>
              </div>
              <div
                className="col-md-8 cards-container">
                <Slider {...settings}>
                  {this.state.boxesArray.map((item, index) =>
                    <CustomSlide key={index}
                                 item={item}/>
                  )}
                </Slider>
                <img src={squarePoints}
                     alt="square points image"/>
              </div>
            </div>
          </div>
        </div>
      </Container>
    )
  }
}

export default LocationSectionHome

const Container = styled.div`
background:#f4f4f4;
position: relative;
z-index: 0;
padding-top: 120px;
padding-bottom: 120px;
margin-top: 150px;
margin-bottom: 48px;
h2{
font-style: normal;
font-weight: 900;
line-height: 64px;
font-size: 56px;
color: rgba(255, 56, 46, 0.95);
}
h5{
font-style: normal;
font-weight: bold;
line-height: 30px;
font-size: 20px;
color: #3D3349;
max-width: 200px;
margin-top: 24px;
}
.slick-slide.slick-center div {
transform: scale(1.1);
}
.cards-container{
img{
display: none;
}
}
 @media (min-width: 576px) { 
.cards-container{
position: relative;
img{
display: block;
position: absolute;
right: -20px;
top: 15px;
z-index: -1020;
}
} 
 }

`

const settings = {
  dots: true,
  infinite: false,
  speed: 800,
  slidesToShow: 3,
  slidesToScroll: 2,
  initialSlide: 0,
  adaptiveHeight: true,
  responsive: [
    {
      breakpoint: 1200,
      settings: {
        slidesToShow: 3,
        slidesToScroll: 2,
      }
    },
    {
      breakpoint: 992,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 2,
      }
    },
    {
      breakpoint: 768,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 2,
      }
    },
    {
      breakpoint: 576,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1
      }
    }
  ]
};