import React from 'react'
import Responsive
  from 'react-responsive-decorator';
import MobileNav from "./MobileNav";
import DesktopNav from "./DesktopNav";
import $ from 'jquery';


class Navbar extends React.Component {
  constructor(props) {
    super(props);


    this.toggle = this.toggle.bind(this);
    this.child = React.createRef();
    this.state = {
      isMobile: false
    };
  }


  componentDidMount() {
    this.props.media({maxWidth: 1286}, () => {
      this.setState({
        isMobile: true
      });
    });

    this.props.media({minWidth: 1286}, () => {
      this.setState({
        isMobile: false
      });
    });
  }

  toggle() {
    this.setState({
      isMobile: !this.state.isMobile,
      scrolling: false,
    });
  }

  scroll(ev, section, offset = 0, isMobile) {
    ev.preventDefault()
    var page = $(`.${section}`)
    var speed = 750
    $('html, body').animate({scrollTop: $(page).offset().top + offset}, speed)
    if (isMobile)
      this.child.current.toggleMobileMenu()
  }

  render() {
    return (
      <div>
        {this.state.isMobile ?
          <MobileNav ref={this.child}>
            <li className="nav-item">
              <span
                onClick={(ev) => this.scroll(ev, 'aboutSection', 0, true)}
              >Accueil</span>
            </li>
            <li
              className="nav-item">
              <span
                onClick={(ev) => this.scroll(ev, 'storageSection', -120, true)}
              >Stockage Particulier</span>
            </li>
            <li className="nav-item">
              <span
                onClick={(ev) => this.scroll(ev, 'storageSection', -120, true)}
              >Stockage Professionnel</span>
            </li>
            <li className="nav-item">
              <span
                onClick={(ev) => this.scroll(ev, 'materialSection', true)}
              >Materiel d'emballage</span>
            </li>
            <li
              className="nav-item contact-button">
              <button
                onClick={(ev) => this.scroll(ev, 'footer', 0, true)}>Contact
              </button>
            </li>
          </MobileNav> :
          <DesktopNav>
            <li className="nav-item">
              <span
                onClick={(ev) => this.scroll(ev, 'header', -300)}
              >Accueil</span>
            </li>
            <li
              className="nav-item">
              <span
                onClick={(ev) => this.scroll(ev, 'storageSection', -120)}
              >Stockage Particulier</span>
            </li>
            <li className="nav-item">
              <span
                onClick={(ev) => this.scroll(ev, 'storageSection', -120)}
              >Stockage Professionnel</span>
            </li>
            <li className="nav-item">
              <span
                onClick={(ev) => this.scroll(ev, 'materialSection')}
              >Materiel d'emballage</span>
            </li>
            <li
              className="nav-item contact-button">
              <button
                onClick={(ev) => this.scroll(ev, 'footer')}>Contact
              </button>
            </li>
          </DesktopNav>
        }
      </div>
    )
  }
}

export default Responsive(Navbar)
