import React from "react";
import {LanguageContext} from "../Context/LanguageProvider";

export default class CustomSlide extends React.Component {
  static contextType = LanguageContext;

  getDescription(item) {
    switch (this.context.lang) {
      case 'fr':
        return item.descriptionFR
      case 'nl':
        return item.descriptionNL
      case 'en':
        return item.descriptionEN;
    }
  }

  render() {
    const {item, index, ...props} = this.props;
    const price = parseFloat(item.price) * 1.21
    const area = parseFloat(item.area)
    return (
      <div {...props} style={styles.container}>
        <div className="" style={{
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'space-between',
          flex: 1
        }}>
          <header>
            <div
              style={styles.volume}>{area.toFixed(0)} m²
            </div>
            <div
              style={styles.description}>{this.getDescription(item)}</div>
          </header>
          <div
            style={styles.price}>€ {price.toFixed(2)}<span
            style={{
              fontSize: 16,
              fontWeight: 400
            }}>/mois</span></div>
        </div>
      </div>
    );
  }
}

const styles = {
  container: {
    display: 'flex',
    height: 290,
    position: 'relative',
    boxShadow: '5px 5px 30px rgba(0, 0, 0, 0.1)',
    borderRadius: 2,
    marginRight: 30,
    marginBottom: 50,
    backgroundColor: 'white',
    marginTop: 50,
    paddingTop: 16,
    paddingBottom: 16,
    paddingLeft: 16,
  },
  description: {
    display: 'block',
    fontSize: 24,
    color: 'rgba(36, 13, 53, 0.5)',
  },
  price: {
    display: 'block',
    fontWeight: 900,
    fontSize: 20,
    color: '#3D3349'
  },
  volume: {
    display: 'block',
    fontWeight: 900,
    fontSize: 20,
    marginBottom: 8,
    color: '#3D3349'
  }
}